export function useBubble() {
    const activeBubble = ref(null);
    function toggleBubble(id) {
        activeBubble.value = activeBubble.value === id ? null : id;
    }
    function closeBubble() {
        activeBubble.value = null;
    }
    return {
        activeBubble,
        toggleBubble,
        closeBubble,
    };
}
